.parallax {
  width: 100%;
  height: 320px;
  background-size: 100%;
  background-position: center;
  margin-bottom: 5px;
  background-repeat: no-repeat;
  font-size: large;
  color: #fff;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  align-content: center;

  .title {
    font-size: 60px;
    font-weight: 400;
    margin: 1rem;
  }
}
